
.App {
  height: 100vh;
  text-align: center;  
    
}

.container{
  
  display: flex;
  justify-content: center;
  text-align: center;  
  align-items: center;
}

.image-container{
  width: 250px;
  height: auto;
  margin: 20px;
  
}
.image-container img{
  border-radius: 50%;
}

.img-fluid{
  width: 100%;
  height: auto;
}

.slider button, .MovieDetails button{
  width: 75%;
  padding: 15px;
  border:none;
  background-color: palevioletred;
  cursor: pointer;
  font-size: 20px;
}

.details-container{
  background-color: #97877a;
  border-radius: 25px 0 ;
  padding: 25px;
}

@media screen and (max-width: 576px) {
  
  .container{  
    display: block;   
    text-align: center;    
  }
  .image-container{
    width: auto;   
    margin: 20px;
    
  }
  
}